.container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  padding: 60px 0 0 60px;
}

.hiddenTitle {
  margin: 100px auto 0 auto;
  padding: 0;
  max-width: 400px;
  width: 100%;

  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  color: #c9d1d3;
}
.titleAndChange {
  display: flex;
  align-items: center;
}

.title {
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 39px;
  margin: 0;
  padding: 0;
}

.list {
  min-width: 420px;
  flex-wrap: wrap;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
}
